import React, { lazy } from "react";
import { withRouter } from "react-router-dom";
import Swal from "sweetalert2";
import {
  getToday,
  localStoragedecryp,
  localStorageencryp,
} from "../../components/helpers/function.jsx";
import { ToastNotification } from "../../components/helpers/notification.jsx";
import { getData, postData } from "../../config/axios.jsx";
import { PageSettings } from "../../config/page-settings.js";
const FormLogin = lazy(() => import("./FormLogin.jsx"));
const Address = process.env.REACT_APP_ADDRESS;

class Login extends React.Component {
  static contextType = PageSettings;

  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      // isLogin: false,
    };
  }

  state = {
    date: "",
  };

  getDate = () => {
    var date = new Date().toDateString();
    this.setState({ date });
  };

  componentDidMount() {
    localStorage.setItem('base_url',JSON.stringify(window.location.href))
    const isAuthenticated = localStoragedecryp("islogin");
    if (isAuthenticated === getToday()) {
      this.props.history.push("/dashboard");
    }
    window.history.pushState(null, document.title, window.location.href);
    window.addEventListener("popstate", function (event) {
      window.history.pushState(null, document.title, window.location.href);
    });

    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
    this.context.handleSetBodyWhiteBg(true);
    this.getDate();
    // console.log(encryptascii('sa'));
    // console.log(decryptascii('BF95'));
  }

  componentWillUnmount() {
    this.context.handleSetPageSidebar(true);
    this.context.handleSetPageHeader(true);
    this.context.handleSetBodyWhiteBg(false);
  }

  onfiled(err) {
    // NotifError(err)
    Swal.fire({
      title: "Oopss !!!",
      text: err.response === undefined ? 'Terjadi Kesalahan Saat Request Data' : err.response.data,
      icon: "info",
    });
    this.setState({ isLoading: false });
  }
  onSubmit(data) {
    let hasil = {
      user_id: data.user_id,
      password: data.password,
    };

    this.setState({ isLoading: true });
    postData("users/login", hasil)
      .then((resposn) => {
        localStorageencryp("userdata", JSON.stringify(resposn.data));
        localStorageencryp("token", resposn.data.token);
        localStorageencryp("islogin", getToday());
      }).then(() => {
        getData('akses-user/id/'+data.user_id).then((res) => {
          localStorage.setItem('menu',JSON.stringify(res.data))
        }).catch((err) => {
          ToastNotification("info",err.response.data)
        })
      })
      .then(() => this.props.history.push("/dashboard"))
      .catch((err) => this.onfiled(err));
  }

  render() {
    return (
      <div className="login login-with-news-feed">
        <div className="news-feed">
          <div
            className="news-image"
            style={{
              backgroundImage: `url(${require("../../assets/img/login-bg/backround-kresno.png")})`,
            }}
          ></div>
          <div className="news-caption text-center">
            {/* <h4 className="caption-title">
              <b>Toko</b> Mas Kresno
            </h4> */}
            <p>
            {Address}
            </p>
          </div>
        </div>
        <div className="right-content">
          <FormLogin
            isLoading={this.state.isLoading}
            onSubmit={(data) => this.onSubmit(data)}
          />
        </div>
      </div>
    );
  }
}

export default withRouter(Login);